    .fade-enter-active, .fade-leave-active {
        transition: opacity .5s
    }

    .fade-enter, .fade-leave-to {
        opacity: 0
    }

    #flag-box {
        min-width: 125px;
        position: relative;
        margin: 10px auto;
    }

    #flag-value {
        width: 100%;
        border: 1px solid #ccc;
        border-bottom: 0;
        border-top-left-radius: 5px;
        border-top-right-radius: 5px;
        outline: none;
        height: inherit;
    }

    #flag-value .code {
        float: left;
        width: calc(100% - 60px);
        height: inherit;
        border-left: 1px solid #ccc;
        font-size: 15px;
    }

    #flag-value .code input {
        background-color: transparent;
        border: none;
        outline: none;
        width: calc(100% - 25px);
        text-align: left;
    }
    #flag-value .code span {
        display: inline-block;
        width: 15px;
        margin-right: -6px;
    }

    .flag-icon {
        float: left;
    }

    .showFlasList {
        border-radius: 5px!important;
        border-bottom: 1px solid #ccc!important;
    }

    .flag-icon i {
        display: inline-block;
        width: 22px;
        height: 16px;
        background-image: url("img/flag_22_16.png");
    }

    .icon {
        position: absolute;
        right: 8px;
    }

    #flag-list {
        max-width: 280px;
        position: absolute;
        width: auto;
        text-align: left;
        padding: 0 15px 0 10px;
        height: 300px;
        overflow: scroll;
        border: 1px solid #ccc;
        z-index: 9;
        background-color: #fff;
    }

    #flag-list li {
        line-height: 1.5;
        white-space: nowrap;
        list-style: none;
        font-size: 12px;
    }

    #flag-list li span {
        color: #333;
        font-weight: bold;
    }

    ::-webkit-scrollbar {
        width:10px;
        height: 10px;
    }
    /* 滚动槽 */
    ::-webkit-scrollbar-track {
        -webkit-box-shadow:inset 0 0 1px rgba(0,0,0,0.3);
        border-radius:5px;
    }
    /* 滚动条滑块 */
    ::-webkit-scrollbar-thumb {
        border-radius:5px;
        background:rgba(0,0,0,0.1);
        -webkit-box-shadow:inset 0 0 2px rgba(0,0,0,0.5);
    }
    ::-webkit-scrollbar-thumb:window-inactive {
        background: rgba(0, 0, 0, 0.4);
    }

    .item {
        padding: 5px 0;
    }

    .item:hover {
        cursor: pointer;
    }

    .item i {
        display: inline-block;
        vertical-align: middle;
        margin-right: 10px;
        width: 22px;
        height: 16px;
        background-image: url("img/flag_22_16.png");
    }
</style>



